.button {
  background: var(--pistachio-colored);
  color: var(--white);
  font-style: normal;
  font-weight: bold;
  font-size: 21px;
  line-height: 26px;
  border: none;
  cursor: pointer;

  &:hover {
    background-color: var(--blue-steel);
  }

  &:active {
    background-color: var(--azure-color);
  }
}
