.page {
  min-width: 320px;
  overflow-x: hidden;
  background-position: top center;
  background-repeat: no-repeat;
  text-align: center;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  height: 100%;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
  cursor: pointer;
}
.page__body {
  min-height: 100%;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}
.main-nav {
  position: relative;
  width: auto;
  min-height: 55px;
}
@media (min-width: 768px) {
  .main-nav {
    width: 768px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    margin: 0 auto;
    width: 1200px;
  }
}
.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}
.main-nav__list a {
  display: block;
  position: relative;
  font-weight: bold;
  font-size: 18px;
  line-height: 40px;
  color: var(--white);
  text-transform: uppercase;
  padding-top: 10px;
  padding-bottom: 5px;
}
@media (min-width: 768px) {
  .main-nav__list a {
    color: var(--black);
    font-size: 14px;
    line-height: 26px;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 3px;
  right: 45px;
  z-index: 2;
  display: block;
  cursor: pointer;
  border: none;
  padding: 0;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 15px;
  width: 25px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
}
@media (min-width: 1200px) {
  .main-nav--closed .main-nav__toggle::before {
    display: none;
  }
}
.main-nav--nojs .main-nav__list {
  position: static;
}
.main-nav--nojs .site-list__item {
  display: block;
}
.main-nav--closed .main-nav__toggle:hover::before {
  opacity: 0.6;
}
.main-nav--closed .main-nav__toggle:active::before {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  left: auto;
  right: 0;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 32px;
  right: 16px;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    top: 52px;
  }
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  opacity: 0.6;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  opacity: 0.3;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
@media (max-width: 767px) {
  .main-nav--opened .page-header__logo {
    display: none;
  }
}
